.card-container {
  height: 310px;
  width: 310px;
  position: relative;
  margin: auto;
}

.card {
  height: 310px;
  width: 310px;
  position: absolute;
  border: 2px solid #300;
}

.card .front {
  height: 310px;
  width: 310px;
  position: absolute;
  overflow: hidden;
  text-align: center;
}

.card.reversed .front {
  display: none;
}

.card .back {
  display: none;
  height: 310px;
  width: 310px;
  text-align: center;
}

.card.reversed .back {
  display: inline;
}

.id {
  color: #333;
  font-size: 1.5em;
  padding-top: 7%;
}

.front-annotation {
  color: #333;
  font-size: 1.5em;
}

.hebrew {
  color: #333;
  font-size: 8em;
  padding-top: 15%;
}

.hebrew img {
  height: 1em;
  margin: -0.2em;
}

.hebrew span.be {
  font-family: 'Frank Ruhl Libre', serif;
}

.translit, .name, .meaning, .type, .sound, .value {
  width: 1px;
  font-size: 1.4em;
  font-weight: bold;
}

.inner {
  font-size: 1.7em;
  color: rgba(200, 10, 10, 1);
  /*font-family: Sans-Serif;*/
}

td.inner.emphasis {
  font-size: 1.7em;
  font-weight: bold;
}

.table {
  position: relative;
  top: 5%;
}
